import React, { Component } from 'react';
import './App.css'; 
// import '/roofpig_and_three.min.js'
import axios from 'axios';


class DeleteUser extends Component { 
  constructor(props){ 
    super(props); 
    this.state = { 
    
       
      
    }
  } 

    deleteRequest = ()=>{ 
        // axios.get(`/deleteUser/${this.props.userID}`).then(r => {this.setState({algorithms: r.data})}); 
        axios.delete(`/deleteUser/${this.props.userID}`);
      } 
    render() {  
      if(!this.props.name){ 
      return( 
        <div>no name</div>
      ); 
      //let algorithms = this.state.algorithms.map(a => <AlgorithmsListItems algID={a.algID} name={a.name} moves={a.moves} scramble={a.scramble} userID={a.userID} />)
      }
      else{
      return (
        <div>      
           
            <button onClick={this.deleteRequest}>Delete User {this.props.name}</button>
        </div>
      
      );  
      }
      
    }
   
  }
  
  export default DeleteUser;